<template >
    <div>
      <!-- 顶部菜单 -->
      <mu-appbar style="width: 100%;" color="primary">
        <mu-button icon slot="left" @click="$router.back(-1)">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
            购物车
        <mu-button color="red" slot="right" v-if="cartNum > 0" @click="openCreateOrderDialog">
          确定下单
        </mu-button>
      </mu-appbar>

      <br/>

      <!-- 菜单 -->
      <mu-list textline="two-line">
        <mu-sub-header>
          <span style="font-size: 25px;color: red">共点:{{cartNumStr}}份菜品,共计:￥{{totalPrice/100}}</span>
          <br/>
          <div v-if="showShareCartBtn">
            <mu-button color="green" slot="right" v-if="cart.share_pwd == ''" @click="openSetSharePwdDialog">
              暗号点餐
            </mu-button>
            <mu-button color="red" slot="right" v-else @click="exitSharePwdDialog">
              退出暗号点餐({{cart.people_num}}人)
            </mu-button>
          </div>
        </mu-sub-header>
        <div v-for="(item,key) in tableData">
          <mu-divider></mu-divider>
        <mu-list-item avatar button :ripple="false"  style="padding-top: 10px;padding-bottom: 10px">
          <mu-list-item-action @click="openAddCartBottomSheet(item)">
            <mu-avatar size="68">
              <img :src="item.mainPic">
            </mu-avatar>
          </mu-list-item-action>
          <mu-list-item-content style="margin-left: 10px" >
            <mu-list-item-title><span style="font-size: 20px">{{item.goods.name}}</span></mu-list-item-title>
            <mu-list-item-sub-title><span style="font-size: 15px">单价:￥{{item.goods.sell_price/100}}</span></mu-list-item-sub-title>
            <mu-list-item-sub-title><span style="color: red;font-size: 15px">总价:￥{{item.goods.sell_price * item.num/100}}</span></mu-list-item-sub-title>
          </mu-list-item-content>
          <mu-list-item-action>
            <mu-button fab color="indigo" small v-if="item.num > 0" @click="removeNum(item)">
              <mu-icon value="remove"></mu-icon>
            </mu-button>
          </mu-list-item-action>
          <span style="width: 30px;font-size: 15px">{{item.num}}</span>
          <mu-list-item-action>
            <mu-button fab color="teal" small @click="addNum(item)">
              <mu-icon value="add"></mu-icon>
            </mu-button>
          </mu-list-item-action>
        </mu-list-item>
        </div>
      </mu-list>
      <!-- 加出购物车的弹框 -->
      <mu-bottom-sheet :open.sync="addCartBottomSheet.open">
        <mu-sub-header>您有什么要特别交待的可以写在下面</mu-sub-header>
        <div style="padding: 20px;text-align: center">
          <el-input
            type="textarea"
            :rows="4"
            placeholder="请输入备注内容"
            v-model="chooseGoods.note">
          </el-input>
          <br/>
          <p>
            <el-tag type="success" @click="addNoteTag('大辣')">大辣</el-tag>
            <el-tag type="success" @click="addNoteTag('中辣')">中辣</el-tag>
            <el-tag type="success" @click="addNoteTag('小辣')">小辣</el-tag>
            <el-tag type="success" @click="addNoteTag('少盐')">少盐</el-tag>
            <el-tag type="success" @click="addNoteTag('多盐')">多盐</el-tag>
            <el-tag type="success" @click="addNoteTag('少油')">少油</el-tag>
            <el-tag type="success" @click="addNoteTag('多油')">多油</el-tag>
          </p>
          <br/>
          <mu-button color="success" @click="sureAddCart">
            <mu-icon left value="shopping_cart"></mu-icon>
            加入购物车
          </mu-button>
        </div>
      </mu-bottom-sheet>

      <!-- 删除弹框 -->
        <mu-dialog title="删除提示" width="360" :open.sync="deleteAlertDialog">
          您确定要移除它吗？
          <mu-button slot="actions" flat color="danger" @click="sureDeleteNum()">确定</mu-button>
          <mu-button slot="actions" flat color="primary" @click="deleteAlertDialog = false">取消</mu-button>
        </mu-dialog>

      <!-- 下单确认弹框 -->
      <mu-dialog title="下单确认" width="360" :open.sync="createOrderAlertDialog">
        就餐人数：<el-input-number v-model="cart.people_num" :min="1" :max="10000" label="人数"></el-input-number>
        <el-input
          style="margin-top: 10px"
          type="textarea"
          :rows="4"
          placeholder="有悄悄话想告诉我们吗"
          v-model="cart.note">
        </el-input>

        <mu-button slot="actions" flat color="primary" @click="createOrderAlertDialog = false">取消</mu-button>
        <mu-button slot="actions" flat color="danger" @click="createOrder()">确定</mu-button>
      </mu-dialog>

      <!-- 一起点餐的功能 -->
      <mu-dialog title="一起点餐" width="360" :open.sync="cartPwdAlertDialog">
         <el-input
          placeholder="大家输入相同暗号即可一起点餐"
          v-model="cart.share_pwd">
        </el-input>

        <mu-button slot="actions" flat color="primary" @click="cartPwdAlertDialog = false">取消</mu-button>
        <mu-button slot="actions" flat color="danger" @click="setSharePwd">确定</mu-button>
      </mu-dialog>

      <!-- 退出共享点餐弹框 -->
      <mu-dialog title="退出提示" width="360" :open.sync="exitShareCartAlertDialog">
        <span v-if="cart.people_num == 1">您是最后一位退出本次“暗号点餐”的客人，退出后将永久解散本次暗号点餐，您确定要这样做吗？</span>
        <span v-else>您确定不和大家一起点餐了吗？</span>
        <mu-button slot="actions" flat color="danger" @click="sureExitShareCart()">确定</mu-button>
        <mu-button slot="actions" flat color="primary" @click="exitShareCartAlertDialog = false">取消</mu-button>
      </mu-dialog>
    </div>
</template >
<script>
  import { baseUrl,baseImgPath,websocketUrl } from '../api/env';

  export default {
    data () {
      return {
        baseUrl,
        baseImgPath,
        websocketUrl,
        loading:false,
        deleteAlertDialog:false,
        createOrderAlertDialog:false,
        tableData: [],
        table_id:3,
        showShareCartBtn:false,
        addCartBottomSheet:{
          open:false,
        },
        chooseGoods:{
          id:0,
          note:"",
          cart_num:0,
        },
        tag_name:"全部菜品",
        cartNum:0,
        totalPrice:0,
        cartNumStr:"0",
        deleteCart:{},
        cart:{
          table_id:0,
          note:"",
          people_num:"1",
          share_pwd:"",
        },
        cartPwdAlertDialog:false,
        exitShareCartAlertDialog:false,
        socket:null,
      }
    },
    created(){
      var storage = window.localStorage;
      this.table_id = storage["table_id"];
      this.initData();
      this.initPlugin();
    },
    methods:{
      initData(){
        var width = document.body.clientWidth;
        this.clos = parseInt(width/255);

        this.loading = true;
        var that = this;
        that.$api.get('/consumer/cart/table/'+this.table_id, {share_pwd:this.cart.share_pwd}, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
                that.totalPrice = 0;
                that.cartNum = 0;
                that.tableData = res.data.carts;
                that.tagsData = [];
                that.tagsData.push("全部菜品");
                that.tableData.forEach(function( val, index ) {
                  that.cartNum += val['num'];
                  that.totalPrice += (val['num'] * val['goods']['sell_price']);
                  that.tableData[index]['mainPic'] = that.baseImgPath+"/api"+val['goods']['pics'][0]['url'];
                });
              this.cartNumStr = ""+this.cartNum;

              if(res.data.share_cart != null){
                this.cart.share_pwd = res.data.share_cart.sharePassword;
                var storage = window.localStorage;
                storage["share_pwd"] = res.data.share_cart.sharePassword;
                this.cart.people_num = storage["share_consumer_count"] = res.data.share_cart.consumerList.length;
                if(this.socket == null){
                  this.initWebsocket();
                }
              }else{
                this.cart.share_pwd = "";
                var storage = window.localStorage;
                storage["share_pwd"] = "";
                if(this.socket != null){
                  this.socket.close();
                  this.socket = null;
                }

              }

            }else{
              //this.$toast.error(res.msg)
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          } else {
            //this.$toast.error('服务器维护中')
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });
      },
      openSetSharePwdDialog(){
        this.cartPwdAlertDialog = true;
      },
      exitSharePwdDialog(){
        this.exitShareCartAlertDialog = true;
      },
      sureExitShareCart(){
        this.loading = true;
        var that = this;
        that.$api.delete('/consumer/cart/table/'+this.table_id+"/share_cart", null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              var storage = window.localStorage;
              storage["share_pwd"] = this.cart.share_pwd = "";
              this.initData();
              this.exitShareCartAlertDialog = false;
            }else{
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          } else {
            //this.$toast.error('服务器维护中')
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });


      },
      setSharePwd(){
        var storage = window.localStorage;
        storage["share_pwd"] = this.cart.share_pwd;
        this.initData();
        this.cartPwdAlertDialog = false;
      },
      sureAddCart(){
        //this.chooseGoods = row;
        this.addCartBottomSheet.open = false;
        var that = this;
        that.$api.post('/consumer/cart/table/'+this.table_id+'/goods/'+this.chooseGoods.id, {num:this.chooseGoods.cart_num,note:this.chooseGoods.note}, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              //计算总价
              //this.cartNum = this.chooseGoods.cart_num;
              //this.cartNumStr = ""+this.cartNum;
              this.initData();
            }else{
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          } else {
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });
      },
      createOrder(){//下单
        this.cart.table_id = this.table_id;
        this.loading = true;
        var that = this;
        that.createOrderAlertDialog = false

        that.$api.post('/consumer/orders/'+that.cart.table_id,that.cart, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              // if (res.data.pay_now) {//需要立即支付
              //
              // } else {
              //     this.$message({
              //       message: "下单成功",
              //       type: 'success'
              //     });
              // }
              this.$router.push({path:'/orderDetail',query:{order_id:res.data.order.id}});
              //Toast.error("下单成功")
              //this.initData();
            }else{
              this.$message({
                message: res.msg,
                type: 'warning'
              });
              // this.$toast.error(res.msg)
            }
          } else {
            // this.$toast.error("服务器维护中")
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });
      },
      chooseTagGoods(tag_name){//按分类选择
          this.tag_name = tag_name;
          this.drawer.open = false;
          this.initData();
      },

      openCreateOrderDialog(){
        this.cart.note = "";
        this.createOrderAlertDialog = true;
      },

      openAddCartBottomSheet(row){
        this.chooseGoods.id = row.goods.id;
        this.chooseGoods.note = row.note == null?"":row.note;
        this.chooseGoods.cart_num = row.num;
        this.addCartBottomSheet.open = true;

      },
      removeNum(row){
        this.chooseGoods.id = row.goods.id;
        this.chooseGoods.note = row.note == null?"":row.note;
        if(row.num == 1){
          this.deleteAlertDialog = true;
        }else{
          row.num--;
          this.chooseGoods.cart_num = row.num;
          this.sureAddCart();
        }
      },
      sureDeleteNum(row){//确定删除
          this.chooseGoods.cart_num = 0;
        this.deleteAlertDialog = false;
          this.sureAddCart();
      },


      addNum(row){
        row.num++;
        this.chooseGoods.id = row.goods.id;
        this.chooseGoods.note = row.note == null?"":row.note;
        this.chooseGoods.cart_num = row.num;

        this.sureAddCart();
      },
      addNoteTag(value){
        this.chooseGoods.note += " " + value;
      },

      in_array(search,array){
        for(var i in array){
          if(array[i]==search){
            return true;
          }
        }
        return false;
      },
      initPlugin(){
        this.loading = true;
        var that = this;
        that.$api.get('/consumer/plugins', null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              var plugins = res.data.plugins;
              plugins.forEach(function( val, index ) {
                //图标：https://blog.csdn.net/boywcx/article/details/85051967
                if(val.uuid == '2e3fddb1f64800a7172119086b6d6687'){//暗号点餐
                  that.showShareCartBtn = true;
                }
              });
            }else{
              //this.$toast.error(res.msg)
            }
          } else {
            //this.$toast.error('服务器维护中')
          }
          this.loading = false;
        });
      },
      initWebsocket() {
        if(typeof(WebSocket) === "undefined"){
          alert("您的浏览器不支持订单提醒推送");//TODO 改为定时拉取
        }else{
          var storage = window.localStorage;
          var user = JSON.parse(storage["user"]);

          var path = "ws:"+websocketUrl+"/websocket/";
          var url = path+(user.user.id);
          // 实例化socket
          this.socket = new WebSocket(url)
          // 监听socket连接
          this.socket.onopen = this.open
          // 监听socket错误信息
          this.socket.onerror = this.error
          // 监听socket消息
          this.socket.onmessage = this.getMessage
          console.log(url)
        }
      },
      open: function () {
        console.log("socket连接成功")
      },
      error: function () {
        console.log("连接错误")
      },
      getMessage: function (msg) {
        var data = msg.data;
        data = JSON.parse(data);
        if (data.type == 5) {//有人加减菜
          if(data.params.action == 'add_goods'){
            this.$message({
              message: data.params.cart.goods.name,
              type: 'success',
              duration:1500
            });
          }else if(data.params.action == 'join_share_cart'){//有人加入，以后增加用户名称时增加
            this.$message({
              message: data.params.user.nickName+" 通过暗号加入了点餐",
              type: 'success',
              duration:1500
            });
          }else if(data.params.action == 'exit_share_cart'){//
            this.$message({
              message: data.params.user.nickName+" 退出了点餐",
              type: 'success',
              duration:1500
            });
          }else if(data.params.action == 'del_share_cart'){//
            var storage = window.localStorage;
            storage["share_pwd"] = this.cart.share_pwd = "";
            this.$message({
              message: "暗号点餐已解散",
              type: 'success',
              duration:1500
            });

          }

          this.initData();
        }
        console.log(data)
      },
      send: function () {
        //this.socket.send(params)
      },
      close: function () {
        console.log("socket已经关闭")
      },
    },
    destroyed () {
      // 销毁监听
      if(this.socket != null){
        this.socket.onclose = this.close();
        this.socket.close();
        this.socket = null;
      }
    }
  }
</script>
<style>
  .demo-list-wrap {
    width: 100%;
    max-width: 360px;
  }
</style>



