/**
 * 配置编译环境和线上环境之间的切换
 *
 * baseUrl: 域名地址
 * routerMode: 路由模式
 * baseImgPath: 图片存放地址
 *
 */
let baseUrl = '/api';
let routerMode = 'hash';
let baseImgPath = '';
let websocketUrl = '//qc.fangwei6.com';
// baseUrl = '//192.168.3.8:8080/api';
// baseImgPath = '//192.168.3.8:8080/api';

if (process.env.NODE_ENV == 'development') {
  baseUrl = '//localhost:8080/api';
  baseImgPath = '//localhost:8080';
  websocketUrl = '//localhost:8080';
  // baseUrl = '//hbtzhx.natappfree.cc/api';
  // baseImgPath = '//hbtzhx.natappfree.cc//api';
}else{
  // baseUrl = '//natapp.fangwei6.com/api';
  // baseImgPath = '//natapp.fangwei6.com/api';
  baseImgPath = '//static.fangwei6.com';//七牛云加速
}

// baseUrl = '//xyy.fangwei6.com/api';
// baseImgPath = '//xyy.fangwei6.com/api';

export {
	baseUrl,
	routerMode,
	baseImgPath,
  websocketUrl
}



