<template >
  <div>
    <mu-appbar style="width: 100%;" color="primary">
      <mu-button icon slot="left" @click="$router.back(-1)">
        <mu-icon value="keyboard_arrow_left"></mu-icon>
      </mu-button>
      我的钱包
    </mu-appbar>

    <br/>
    <el-row style="height: 100px;text-align: center;margin-top: 30px">
      <span style="font-size: 30px;">钱包余额： ￥<span>{{balance}}</span></span>
    </el-row>
    <mu-divider></mu-divider>
    <mu-sub-header><span style="font-size: 20px">充值</span></mu-sub-header>
    <el-row>
      <el-button type="success" style="margin-top: 10px;width: 100px;height: 80px" v-for="(item,key) in tableData" @click="createdOrder(item)" :key="key">
          充￥{{item.chargePrice}}<span v-if="item.chargePrice != item.totalPrice">得￥{{item.totalPrice}}</span>
          <div v-if="item.tag != null">
            <br/>
            {{item.tag}}
          </div>
      </el-button>
    </el-row>

    <!-- 选择支付方式 -->
    <mu-dialog title="支付方式" width="360" :open.sync="payTypeDialog" @close="closePayPop">
      <span v-if="paymentConfigs.length == 0" style="font-size: 30px">请前往收银台付款</span>
      <a :href="baseUrl+'/payment/pay/'+item.id+'?order_id='+order_id+'&token='+token" target="_blank"  v-for="(item, index) in paymentConfigs" :key="index" color="success">
        <mu-chip class="demo-chip">
          {{item.name}}
        </mu-chip>
      </a>
    </mu-dialog>
  </div>
</template>
<script>
  import { baseUrl,baseImgPath } from '../api/env';

  export default {
    data () {
      return {
        baseUrl,
        baseImgPath,
        loading:false,
        tableData: [],
        payTypeDialog:false,
        payNow:false,
        paymentConfigs:[],
        token:null,
        order_id:0,
        balance:0,
      }
    },
    created(){
      var storage = window.localStorage;
      this.token = storage["access_token"];
      this.initData();
    },
    methods:{
      initData(){
        this.loading = true;
        var that = this;
        that.$api.get('/consumer/charge_item', null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              that.tableData = res.data.charge_items;
              that.balance = res.data.balance/100;
              //this.$toast.error(res.msg);
            }else{
              //this.$toast.error(res.msg);
            }
          } else {
            //this.$toast.error('服务器维护中');
          }
          this.loading = false;
        });
      },
      goPay(){//弹出支付选项
        this.loading = true;
        var that = this;
        that.$api.get('/consumer/payment_config/can_used',null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              that.paymentConfigs = res.data.paymentsConfigs;
              that.payTypeDialog = true;
              console.log(response);
            }else{
              //this.$toast.error(res.msg)
            }
          } else {
            //this.$toast.error('服务器维护中')
          }
          this.loading = false;
        });
      },
      closePayPop(){
        this.initData();
      },
      createdOrder(row){//生成支付订单
        this.loading = true;
        var that = this;
        that.$api.post('/consumer/charge_item/'+row.id,null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              this.order_id = res.data.order_id;
              this.goPay();
            }else{
              //this.$toast.error(res.msg)
            }
          } else {
            //this.$toast.error('服务器维护中')
          }
          this.loading = false;
        });
      },
      dateTimeFormat(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        /* 在日期格式中，月份是从0开始的，因此要加0
         * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
         * */
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 拼接
        return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
        //return year+"-"+month+"-"+day;
      },
      in_array(search,array){
        for(var i in array){
          if(array[i]==search){
            return true;
          }
        }
        return false;
      },

    },
  }
</script>



