<template >
    <div>
      <!-- 顶部菜单 -->
      <mu-appbar style="width: 100%;" color="primary">
        <mu-button icon slot="left" @click="$router.back(-1)">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
        订单详情
      </mu-appbar>
      <br/>

      <mu-paper :z-depth="1">
        <mu-list>
          <mu-sub-header><span style="font-size: 20px">基本信息</span></mu-sub-header>
          <mu-list-item avatar button :ripple="false">
            <mu-list-item-action>
              订单号:
            </mu-list-item-action>
            <mu-list-item-title>{{order.orderNo}}</mu-list-item-title>
          </mu-list-item>
          <mu-list-item avatar button :ripple="false">
            <mu-list-item-action>
              时间:
            </mu-list-item-action>
            <mu-list-item-title>{{order.createTime}}</mu-list-item-title>
          </mu-list-item>
          <mu-list-item avatar button :ripple="false">
            <mu-list-item-action>
              人数:
            </mu-list-item-action>
            <mu-list-item-title>{{order.peopleNum}}人</mu-list-item-title>
          </mu-list-item>
          <mu-list-item avatar button :ripple="false">
            <mu-list-item-action>
              总价:
            </mu-list-item-action>
            <mu-list-item-title>￥{{order.totalPrice/100}}元</mu-list-item-title>
          </mu-list-item>
          <mu-list-item avatar button :ripple="false" v-if="order.totalPrice != order.realPrice">
            <mu-list-item-action>
              实付价:
            </mu-list-item-action>
            <mu-list-item-title>￥{{order.realPrice}}元</mu-list-item-title>
          </mu-list-item>
        </mu-list>

        <mu-divider></mu-divider>
        <mu-list>
          <mu-sub-header><span style="font-size: 20px">支付信息</span></mu-sub-header>
          <mu-list-item avatar button :ripple="false">
            <mu-list-item-action>
              状态:
            </mu-list-item-action>
            <mu-list-item-title>
              {{order.payStatusStr}}
            </mu-list-item-title>
            <mu-list-item-title v-if="order.payStatus == 1" style="height: 50px">
              <mu-button color="success" @click="goPay('open')">立即支付</mu-button>
            </mu-list-item-title>
          </mu-list-item>
          <mu-list-item avatar button :ripple="false" v-if="order.payStatus > 1">
            <mu-list-item-action>
              时间:
            </mu-list-item-action>
            <mu-list-item-title>{{order.payTime}}</mu-list-item-title>
          </mu-list-item>
          <mu-list-item avatar button :ripple="false" v-if="order.payStatus > 1">
            <mu-list-item-action>
              方式:
            </mu-list-item-action>
            <mu-list-item-title>{{order.payTypeStr}}</mu-list-item-title>
          </mu-list-item>
        </mu-list>

        <mu-divider></mu-divider>
        <mu-list v-if="order.type == 1">
          <mu-sub-header><span style="font-size: 20px">菜品列表</span></mu-sub-header>
          <div v-for="(item,key) in orderGoods">
            <mu-divider></mu-divider>
            <mu-list-item avatar button :ripple="false"  style="margin-top: 10px;padding-top: 20px;padding-bottom: 20px">
              <mu-list-item-action @click="openAddCartBottomSheet(item)">
                <mu-avatar size="68">
                  <img :src="item.mainPic">
                </mu-avatar>
              </mu-list-item-action>
              <mu-list-item-content style="margin-left: 10px" >
                <mu-list-item-title><span style="font-size: 20px">{{item.goods.name}}</span></mu-list-item-title>
                <mu-list-item-sub-title><span style="font-size: 15px">单价:￥{{item.goods.sell_price/100}}</span></mu-list-item-sub-title>
                <mu-list-item-sub-title><span style="color: red;font-size: 15px">数量:{{item.count}}</span></mu-list-item-sub-title>
                <mu-list-item-sub-title v-if="item.note != null && item.note != ''"><span style="font-size: 15px">备注：{{item.note}}</span></mu-list-item-sub-title>
              </mu-list-item-content>

              <span style="font-size: 15px">总价:￥{{item.count*item.goods.sell_price/100}}</span>
            </mu-list-item>
          </div>

        </mu-list>
      </mu-paper>


      <!-- 选择支付方式 -->
      <mu-dialog title="支付方式" width="360" :open.sync="payTypeDialog" @close="closePayPop">
        <span v-if="paymentConfigs.length == 0" style="font-size: 30px">请前往收银台付款</span>
          <a :href="baseUrl+'/payment/pay/'+item.id+'?order_id='+order.id+'&token='+token" target="_blank"  v-for="(item, index) in paymentConfigs" :key="index" color="success">
          <mu-chip class="demo-chip">
          {{item.name}}
        </mu-chip>
        </a>
      </mu-dialog>
    </div>
</template >
<script>
  import { baseUrl,baseImgPath } from '../api/env';
  export default {
    data () {
      return {
        baseUrl,
        baseImgPath,
        loading:false,
        tableData: [],
        order_id:0,
        table_id:0,
        orderGoods:[],
        order:[],
        payTypeDialog:false,
        payNow:false,
        paymentConfigs:[],
        token:null,
      }
    },
    created(){
      this.order_id = this.$route.query.order_id;

      var storage = window.localStorage;
      this.table_id = storage["table_id"];
      this.token = storage["access_token"];
      this.initData("init");
    },
    methods:{
      initData(actint){
        this.loading = true;
        var that = this;
        that.$api.get('/consumer/orders', {order_id:this.order_id,table_id:this.table_id}, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              that.order = res.data.order;
              that.order['createTime'] = that.dateTimeFormat(that.order['createTime']);
              if(that.order['payTime'] != null){
                that.order['payTime'] = that.dateTimeFormat(that.order['payTime']);
              }
              if(that.order.type == 1){
                  this.orderGoods = res.data.orderGoods;
                  that.orderGoods.forEach(function( val, index ) {
                    that.orderGoods[index]['mainPic'] = that.baseImgPath+"/api"+val['goods']['pics'][0]['url'];
                  });
                  if(actint == "init"){
                      this.goPay("init");
                  }
              }

            }else{
              //this.$toast.error(res.msg)
            }
          } else {
            //this.$toast.error('服务器维护中')
          }
          this.loading = false;
        });
      },
      goPay(action){//弹出支付选项
        if(this.order.payStatus > 1){//
          return;
        }
        this.loading = true;
        var that = this;
        that.$api.get('/consumer/payment_config/can_used',null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              that.payNow = res.data.pay_now;
              that.paymentConfigs = res.data.paymentsConfigs;
              if(action == "init" && that.payNow){
                that.payTypeDialog = true;
              }else if(action == "open"){
                that.payTypeDialog = true;
              }

              console.log(response);
            }else{
              //this.$toast.error(res.msg)
            }
          } else {
            //this.$toast.error('服务器维护中')
          }
          this.loading = false;
        });
      },
      pay(row){
        var storage = window.localStorage;
        var token = storage["access_token"];
        location.href= this.baseUrl+"/payment/pay/"+row.id+"?order_id="+this.order_id+"&token="+token+"&t="+(new Date().getTime());
      },
      closePayPop(){
        this.initData("close");
      },
      dateTimeFormat(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        /* 在日期格式中，月份是从0开始的，因此要加0
         * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
         * */
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 拼接
        return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
        //return year+"-"+month+"-"+day;
      },
      in_array(search,array){
        for(var i in array){
          if(array[i]==search){
            return true;
          }
        }
        return false;
      },

    },
  }
</script>
<style>
  .gridlist-demo{
    overflow-y: auto;
  }
</style>



