<template >
    <div>
      <!-- 顶部菜单 -->
      <mu-appbar style="width: 100%;" color="primary">
        <mu-button icon slot="left" @click.native="drawer.open = !drawer.open">
          <mu-icon value="menu"></mu-icon>
        </mu-button>
            {{tag_name}}
          <!--<mu-button flat slot="right" @click="goOrders()">-->
            <!--<mu-icon value="assignment"></mu-icon>-->
          <!--</mu-button>-->
          <mu-button flat slot="right" @click="goCart()">
            <mu-badge :content="cartNumStr" circle color="secondary" v-if="cartNum > 0">
              <mu-icon value="shopping_cart"></mu-icon>
            </mu-badge>
            <mu-icon value="shopping_cart" v-else></mu-icon>
          </mu-button>

      </mu-appbar>

      <!-- 侧边栏 -->
      <mu-drawer :open.sync="drawer.open" :docked="drawer.docked">

        <mu-sub-header>选择菜品分类</mu-sub-header>
        <mu-list>
          <mu-list-item button v-for="(item,key) in tagsData" :key="key" @click="chooseTagGoods(item)">
            <mu-list-item-title>{{item}}</mu-list-item-title>
          </mu-list-item>
        </mu-list>
      </mu-drawer>

      <br/>
      <!-- 菜单 -->
      <mu-grid-list :cols="clos">
        <mu-grid-tile v-for="tile, index in tableData" :key="index">
          <img :src="tile.mainPic" style="width: 100%;height: 100%">
          <span slot="title">{{tile.name}}</span>
          <span slot="subTitle">￥<b>{{tile.sell_price/100}}</b><span v-if="tile.cart_num > 0"> x {{tile.cart_num}}</span> </span>
          <mu-button slot="action" large color="red" icon @click="openAddCartBottomSheet(tile)">
            <mu-icon value="shopping_cart" v-if="tile.cart_num > 0"></mu-icon>
            <mu-icon value="remove_shopping_cart" v-else></mu-icon>
          </mu-button>

        </mu-grid-tile>
      </mu-grid-list>


      <!-- 加出购物车的弹框 -->
      <mu-bottom-sheet :open.sync="addCartBottomSheet.open">
        <mu-sub-header>您有什么要特别交待的可以写在下面</mu-sub-header>
        <div style="padding: 20px;text-align: center">
          <el-input
            type="textarea"
            :rows="4"
            placeholder="请输入备注内容"
            v-model="chooseGoods.note">
          </el-input>
          <br/>
          <p>
            <el-tag type="success" @click="addNoteTag('大辣')">大辣</el-tag>
            <el-tag type="success" @click="addNoteTag('中辣')">中辣</el-tag>
            <el-tag type="success" @click="addNoteTag('小辣')">小辣</el-tag>
            <el-tag type="success" @click="addNoteTag('少盐')">少盐</el-tag>
            <el-tag type="success" @click="addNoteTag('多盐')">多盐</el-tag>
            <el-tag type="success" @click="addNoteTag('少油')">少油</el-tag>
            <el-tag type="success" @click="addNoteTag('多油')">多油</el-tag>
          </p>
          <br/>
          <mu-button color="success" @click="sureAddCart">
            <mu-icon left value="shopping_cart"></mu-icon>
            加入购物车
          </mu-button>
        </div>
      </mu-bottom-sheet>

      <mu-bottom-sheet :open.sync="callLogDialog">
        <div style="padding: 20px;text-align: center">
          <el-input
            type="textarea"
            :rows="4"
            placeholder="请输入您想要呼叫的服务内容"
            v-model="callLog.content">
          </el-input>
          <br/>
          <p>
            <el-tag type="success" @click="addNCallTag(item.content)" v-for="(item,key) in callTags" :key="key">{{item.content}}</el-tag>
          </p>
          <br/>
          <mu-button  @click="callLogDialog = false">
            取消
          </mu-button>
          <mu-button color="success" @click="saveCallLog">
            确定
          </mu-button>
        </div>
      </mu-bottom-sheet>

      <!-- 底部浮动按钮 -->
      <vue-fab
        :mainBtnColor="mainBtnColor"
        @clickMainBtn="clickMainBtn"
        icon="face"
        fabAnimateBezier="ease-in-out"
        fabItemAnimate="alive"
        fabAliveAnimateBezier="ease"
      >
        <fab-item
          size="big"
          v-for="(item, key) in menu" :key="key"
          :idx="item.id"
          :title="item.title"
          :color="item.color"
          :icon="item.icon"
          @clickItem="clickItem(item)" />
      </vue-fab>
    </div>
</template >
<script>
  import { baseUrl,baseImgPath,websocketUrl} from '../api/env';
  export default {
    data () {
      return {
        baseUrl,
        baseImgPath,
        websocketUrl,
        loading:false,
        tableData: [],
        tagsData: [],
        table_id:3,
        clos:2,//根据客户端宽度计算分列
        socket:null,
        drawer:{
          open:false,
          docked:false,
        },
        addCartBottomSheet:{
          open:false,
        },
        chooseGoods:{
          id:0,
          note:"",
          cart_num:0,
        },
        tag_name:"全部菜品",
        cartNum:0,
        cartNumStr:"0",

        callLog:{
          content:"",
        },
        callTags:[],
        callLogDialog:false,
        mainBtnColor: '#FF0099',
        menu: [

          {
            id:2,
            icon: 'event_note',
            title: '查看订单',
            color: '#ee9390'
          }

        ],
      }

    },
    created(){
      this.table_id = this.$route.query.table_id;
      var storage = window.localStorage;
      storage["access_token"] = this.$route.query.token;
      storage["table_id"] = this.table_id;
      this.initPlugin();
      this.initData();

    },
    methods:{
      initPlugin(){
        this.loading = true;
        var that = this;
        that.$api.get('/consumer/plugins', null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              that.plugins = res.data.plugins;
              that.plugins.forEach(function( val, index ) {
                //图标：https://blog.csdn.net/boywcx/article/details/85051967

                if(val.uuid == '228add581e703d931dd08d8edd8283d9'){//充值
                    that.menu.push({
                      id:3,
                      icon: 'money',
                      title: '钱包',
                      color: '#ee9390'
                    });
                }else if(val.uuid == '28a605845f825b7bd950e423ea90c6d8'){//呼叫
                    that.menu.push({
                      id:1,
                      icon: 'phone_in_talk',
                      title: '呼叫服务员',
                      color: '#ff9900'
                    });
                  }
              });
            }else{
              //this.$toast.error(res.msg)
            }
          } else {
            //this.$toast.error('服务器维护中')
          }
          this.loading = false;
        });
      },
      initData(){
        var width = document.body.clientWidth;
        this.clos = parseInt(width/200);
        var storage = window.localStorage;

       var share_pwd = storage["share_pwd"];
        this.loading = true;
        var that = this;
        that.$api.get('/consumer/cart/table/'+this.table_id,null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              if(res.data.share_cart != null){
                var storage = window.localStorage;
                storage["user"] = JSON.stringify(res.data.user);
                if(this.socket == null){
                  this.initWebsocket();
                }
              }else{
                if(this.socket != null){
                  this.socket.close();
                  this.socket = null;
                }
              }




              if(res.data.share_cart != null){
                var storage = window.localStorage;
                storage["share_consumer_count"] = res.data.share_cart.consumerList.length;
              }

              if(that.tag_name == "全部菜品"){
                that.tableData = res.data.goods;
                that.tagsData = [];
                that.tagsData.push("全部菜品");
                that.tableData.forEach(function( val, index ) {
                  that.tableData[index]['mainPic'] = that.baseImgPath+"/api"+val['pics'][0]['url'];
                  var goodsTags = val['goodsTags'];
                  goodsTags.forEach(function( val2, index2) {
                    if(!that.in_array(val2['name'],that.tagsData)){
                      that.tagsData.push(val2['name']);
                    }
                  });
                });
              }else{
                var goodsData = res.data.goods;
                that.tagsData = [];
                that.tagsData.push("全部菜品");
                that.tableData = [];
                goodsData.forEach(function( val, index ) {
                  goodsData[index]['mainPic'] = that.baseImgPath+"/api"+val['pics'][0]['url'];
                  var goodsTags = val['goodsTags'];
                  goodsTags.forEach(function( val2, index2) {
                    if(!that.in_array(val2['name'],that.tagsData)){
                      that.tagsData.push(val2['name']);
                    }
                    if(val2['name'] == that.tag_name && !that.in_array(val,that.tableData)){//存在同类型的，则加入
                      that.tableData.push(val);
                    }
                  });

                });



              }

              console.log("=======");
              console.log(that.tagsData);
              var carts = res.data.carts;
              this.cartNum = 0;
              carts.forEach(function( val, index ) {
                that.cartNum += val['num'];
              });
              this.cartNumStr = ""+this.cartNum;

            }else{
              //this.$toast.error(res.msg)
            }
          } else {
            //this.$toast.error('服务器维护中')
          }
          this.loading = false;
        });
      },
      sureAddCart(){
        //this.chooseGoods = row;
        this.addCartBottomSheet.open = false;
        var that = this;
        that.$api.post('/consumer/cart/table/'+this.table_id+'/goods/'+this.chooseGoods.id, {num:this.chooseGoods.cart_num,note:this.chooseGoods.note}, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              //计算总价
              this.cartNum ++;
              this.cartNumStr = ""+this.cartNum;
            }else{
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          } else {
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });
      },
      chooseTagGoods(tag_name){//按分类选择
          this.tag_name = tag_name;
          this.drawer.open = false;
          this.initData();
      },

      openAddCartBottomSheet(row){
        row.cart_num ++;
        this.chooseGoods.id = row.id;
        this.chooseGoods.note = row.note == null?"":row.note;
        this.chooseGoods.cart_num = row.cart_num;
        if(row.cart_num == 0){
          this.addCartBottomSheet.open = true;
        }else{
          this.sureAddCart();
        }

      },

      addNoteTag(value){
        this.chooseGoods.note += " " + value;
      },
      addNCallTag(value){
        this.callLog.content += " " + value;
      },

      goCart(){
        this.$router.push('/carts');
      },
      goOrders(){
        this.$router.push('/orders');
      },
      goBalance(){
        this.$router.push('/balance');
      },
      clickItem: function (item) {
        console.log('idx: ' + item.id)
        if(item.id == 1){
          this.openCallDialog();
        }else if(item.id == 2){
          this.goOrders();
        }else if(item.id == 3){
          this.goBalance();
        }
      },
      clickMainBtn: function () {
        console.log('clickMainBtn')
      },

      openCallDialog(){//打开呼叫页面
        this.callLogDialog = false;
        this.loading = true;
        var that = this;
        that.$api.get('/consumer/call', null, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              this.callTags = res.data.callTags;
              this.callLog.content = "";
              this.callLogDialog = true;
            }else{
              this.$message({
                message: "本功能即将上线",
                type: 'warning'
              });
            }
          } else {
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });
      },
      saveCallLog(){
        if(this.callLog.content == ""){
          this.$message({
            message: "请输入您要呼叫的服务内容",
            type: 'warning'
          });
        }
        //this.chooseGoods = row;
        this.callLogDialog = false;
        var that = this;

        that.$api.post('/consumer/call/table/'+this.table_id, this.callLog, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              this.$message({
                message: "呼叫成功",
                type: 'success'
              });
            }else{
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          } else {
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });
      },
      in_array(search,array){
        for(var i in array){
          if(array[i]==search){
            return true;
          }
        }
        return false;
      },
      initWebsocket() {
        if(typeof(WebSocket) === "undefined"){
          alert("您的浏览器不支持订单提醒推送");//TODO 改为定时拉取
        }else{
          var storage = window.localStorage;
          var user = JSON.parse(storage["user"]);

          var path = "ws:"+websocketUrl+"/websocket/";
          var url = path+(user.user.id);
          // 实例化socket
          this.socket = new WebSocket(url)
          // 监听socket连接
          this.socket.onopen = this.open
          // 监听socket错误信息
          this.socket.onerror = this.error
          // 监听socket消息
          this.socket.onmessage = this.getMessage
          console.log(url)
        }
      },
      open: function () {
        console.log("socket连接成功")
      },
      error: function () {
        console.log("连接错误")
      },
      getMessage: function (msg) {
        var data = msg.data;
        data = JSON.parse(data);

        console.log(data)
        if (data.type == 5) {//有人加减菜
          this.initData();
          //this.$toast.success(data.params.cart.goods.name);
          if(data.params.action == 'add_goods'){
            this.$message({
              message: data.params.cart.goods.name,
              type: 'success',
              duration:1500
            });
          }else if(data.params.action == 'join_share_cart'){//有人加入，以后增加用户名称时增加
            this.$message({
              message: data.params.user.nickName + "通过暗号加入了点餐",
              type: 'success',
              duration:1500
            });
          }else if(data.params.action == 'exit_share_cart'){//有人加入，以后增加用户名称时增加
            this.$message({
              message: data.params.user.nickName+" 退出了点餐",
              type: 'success',
              duration:1500
            });
          }else if(data.params.action == 'del_share_cart'){//有人加入，以后增加用户名称时增加
            this.$message({
              message: "暗号点餐已解散",
              type: 'success',
              duration:1500
            });
            this.sureExitShareCart();
          }

        }
      },
      send: function () {
        //this.socket.send(params)
      },
      close: function () {
        console.log("socket已经关闭")
      },
    },
    destroyed () {
      // 销毁监听
      if(this.socket != null){
        this.socket.onclose = this.close;
        this.socket.close();
        this.socket = null;
      }
    }
  }
</script>
<style>
  .gridlist-demo{
    overflow-y: auto;
  }
</style>



