import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import Index from '../pages/index.vue';
import Carts from '../pages/carts.vue';
import Orders from '../pages/orders.vue';
import OrderDetail from '../pages/orderDetail.vue';
import Balance from '../pages/balance.vue';

export default new Router({
  routes: [
    {path: '/', name: '测试', component: Index,
      children: [
        // {path: '', name: '', component: CurriculumTypes},
        // {path: '/curriculumTypes', name: '课程', component: CurriculumTypes},


      ]
    },
     {path: '/carts', name: '购物车', component: Carts},
     {path: '/orders', name: '订单列表', component: Orders},
     {path: '/orderDetail', name: '购物车', component: OrderDetail},
      {path: '/balance', name: '我的钱包', component: Balance},

    // {path: '/wxLogin', name: '绑定老师微信帐号', component: WxLogin},
    // {path: '/chooseSchools', name: '绑定老师微信帐号', component: ChooseSchools},

  ]
})



