<template >
    <div>
      <!-- 顶部菜单 -->
      <mu-appbar style="width: 100%;" color="primary">
        <mu-button icon slot="left" @click="$router.back(-1)">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
            订单列表
      </mu-appbar>

      <br/>

      <!-- 菜单 -->
      <mu-list textline="two-line">
        <div v-for="(item,key) in tableData">
          <mu-divider></mu-divider>
        <mu-list-item avatar button :ripple="false"  style="padding-top: 10px;padding-bottom: 10px">
          <!--<mu-list-item-action>-->
            <!--<mu-avatar size="68">-->
              <!--<img :src="item.mainPic">-->
            <!--</mu-avatar>-->
          <!--</mu-list-item-action>-->
          <mu-list-item-content>
            <mu-list-item-title><span style="font-size: 20px">单号：{{item.orderNo}}</span></mu-list-item-title>
            <mu-list-item-sub-title><span style="color: red;font-size: 15px">总价：￥{{item.realPrice/100}}</span> ({{item.payStatusStr}})</mu-list-item-sub-title>
            <mu-list-item-sub-title><span style="font-size: 15px" >创建时间：{{item.createTime}}</span></mu-list-item-sub-title>
          </mu-list-item-content>
          <mu-list-item-action>
            <mu-button fab color="indigo" small @click="goOrderDetail(item)">
              <mu-icon value="info"></mu-icon>
            </mu-button>
          </mu-list-item-action>
        </mu-list-item>
        </div>
      </mu-list>

    </div>
</template >
<script>
  import { baseUrl,baseImgPath } from '../api/env';

  export default {
    data () {
      return {
        baseUrl,
        baseImgPath,
        loading:false,
        tableData: [],
        table_id:3,
      }
    },
    created(){
      var storage = window.localStorage;
      this.table_id = storage["table_id"];
      this.initData();
    },
    methods:{
      initData(){
        var width = document.body.clientWidth;
        this.clos = parseInt(width/255);

        this.loading = true;
        var that = this;
        that.$api.get('/consumer/orders',{table_id:this.table_id}, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
                that.tableData = res.data.orders.content;

                that.tableData.forEach(function( val, index ) {
                  val['createTime'] = that.dateTimeFormat(val['createTime']);
                });
            }else{
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          } else {
            //this.$toast.error('服务器维护中')
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });
      },


      dateTimeFormat(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        /* 在日期格式中，月份是从0开始的，因此要加0
         * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
         * */
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 拼接
        return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
        //return year+"-"+month+"-"+day;
      },
      goOrderDetail(row){
        this.$router.push({path:'/orderDetail',query:{order_id:row.id}});
      },
      in_array(search,array){
        for(var i in array){
          if(array[i]==search){
            return true;
          }
        }
        return false;
      },

    },
  }
</script>
<style>
  .demo-list-wrap {
    width: 100%;
    max-width: 360px;
  }
</style>



